import React, {Component} from 'react';
import {Button} from 'react-bootstrap';
import {FormGroup, Label, Input} from 'reactstrap';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faPaperPlane,faUserCircle,faMap,faPhoneAlt} from "@fortawesome/free-solid-svg-icons";
import axios from 'axios';




export default class Form extends Component {
   
state={
    name:'',
    email:'',
    subject:'',
    message:'',
    sent: false
}


// handle input
handleName = (e) => {
    this.setState({
        name:e.target.value
    })
}

handleEmail = (e) => {
    this.setState({
        email:e.target.value
    })
}

handleSubject = (e) => {
    this.setState({
        subject:e.target.value
    })   
}

handleMassage = (e) => {
    this.setState({
       message:e.target.value
    })
}

//End of handle input

formSubmit =(e) => {
    e.preventDefault();

    let data = {
        name:this.state.name,
        email:this.state.email,
        subject:this.state.subject,
        message:this.state.message,

    }

axios.post('/api/forma', data)
.then(res=>{
        this.setState({
            sent:true,
        },this.resetForm())
})

.catch(()=>{
    console.log('message not sent');
})

}

//for reseting intial data
resetForm =()=>{
    this.setState({
        name:'',
        email:'',
        subject:'',
        message:''
    })

    setTimeout(()=>{
        this.setState({
            sent:false,
        })
    }, 3000)
}


//End of for reseting intial data


render(){
    return(
   
        <div className="container">
            <form onSubmit={this.formSubmit}>
                           <FormGroup>                               
                               <Label>Full Name</Label>
                               <Input type="text" name="name" id="nameid" placeholder="Enter your full name" value={this.state.name} onChange={this.handleName} required/>
                           </FormGroup>
                           
                           <FormGroup>
                               <Label>Email</Label>
                               <Input type="email" name="email" id="emailid" placeholder="with a placeholder" value={this.state.email} onChange={this.handleEmail} />
                           </FormGroup>

                           <FormGroup>
                               <Label>Subject</Label>
                               <Input type="text" name="subject" id="subjectid" placeholder="Subject" value={this.state.subject} onChange={this.handleSubject}/>
                           </FormGroup>

                           <FormGroup>
                               <Label>Message</Label>
                               <Input type="textarea" name="message" id="messageid" rows="5" value={this.state.message} onChange={this.handleMassage}/>                               
                           </FormGroup>
                           
                           <div className={this.state.sent ? 'msg msgAppear':'msg'}>Message has been sent</div>

                           <Button type="submit" variant="outline-warning" className="Button">
                           Send message  <FontAwesomeIcon  icon={faPaperPlane}></FontAwesomeIcon>  
                           </Button>
            </form>
           
        </div>

    )
}    
}

